import React, { Component } from "react";
import moment from "moment";
import { connect } from "react-redux";
import { deleteComment } from "../../store/actions/postActions";
class CommentSummary extends Component {
  state = {
    commentId: "",
    postId: "",
  };
  handleDelete = (e) => {
    this.setState(
      {
        commentId: e.target.dataset.commentid,
        postId: e.target.dataset.postid,
      },
      () => {
        this.props.deleteComment(this.state);
      }
    );
  };
  render() {
    const { comment, username, auth } = this.props;
    return (
      <div className="comment">
        <div className="card col-md-12">
          <div className="row m-2">
            <img
              width="20"
              height="20"
              src="data:image/png;base64,"
              alt=""
              className="rounded-circle border-dark mr-2"
            />
            <span>
              <strong>{username}&nbsp;</strong>
            </span>
            <span> {comment[1].content}</span>
            <div className="grey-text note-date mb-1 ml-4">
              {moment(comment[1].createAt.toDate()).fromNow()}
            </div>
            <div className="right">
              <a>
                {comment[1].userId === auth.uid ? (
                  <i
                    className="fas fa-trash mr-2"
                    data-postid={comment[1].postId}
                    data-commentid={comment[0]}
                    onClick={this.handleDelete}
                  ></i>
                ) : null}
              </a>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    deleteComment: (comment) => dispatch(deleteComment(comment)),
  };
};
export default connect(null, mapDispatchToProps)(CommentSummary);
